import {requestUpdateStrategy} from '../api/API'
export default {
  name: 'AutoSellConfig',
  props: {
    show: {type: Boolean},
    strategyData: {type: Object}
  },
  watch: {
    show (newValue) {
      this.popupVisible = newValue
      this.ying = this.$props.strategyData.tp
      this.sun = this.$props.strategyData.sl
    }
  },
  data () {
    return {
      popupVisible: false,
      ying: 0.00,
      sun: 0.00,
      xinYongJin: ''
    }
  },
  methods: {
    onCancelClicked () {
      this.$emit('close')
    },
    reduceFinal () {
      if (this.ying - 0.01 > 0) {
        this.ying = (parseFloat(this.ying) - 0.01).toFixed(2)
      }
    },
    addFinal () {
      this.ying = (parseFloat(this.ying) + 0.01).toFixed(2)
    },
    reduceFinal2 () {
      if (this.sun - 0.01 > 0) {
        this.sun = (parseFloat(this.sun) - 0.01).toFixed(2)
      }
    },
    addFinal2 () {
      this.sun = (parseFloat(this.sun) + 0.01).toFixed(2)
    },
    async updateConfig () {
      let res = await requestUpdateStrategy(this.$store.state.User.token, this.$props.strategyData.id, this.ying, this.sun)
      if (res.code === 200) {
        this.$emit('updateComplete')
        this.onCancelClicked()
      }
    }
  }
}



