import Header from '../../components/Header'
import BottomBar from '../../components/BottomBar'
import AutoSellConfig from '../../components/AutoSellConfig'
import {queryOpenedStrategies, queryClosedStrategies, queryUserAssets, requestCloseAutoDelayStrategy, requestStrategySale} from '../../api/API'
import MathUtil from '../../utils/MathUtil'
import ScreenUtil from '../../utils/ScreenUtil'
import Vue from 'vue'
import { Popup } from 'mint-ui'
Vue.component(Popup.name, Popup)
window.MathUtil = MathUtil
export default {
  name: 'strategy',
  components: {
    Header,
    BottomBar,
    AutoSellConfig,
    ClosedDetailContent: () => import('./ClosedDetailContent.vue')
  },
  data () {
    return {
      selectedTab: 0,
      showAutoSell: false,
      confirmSellShow: false,
      userAssets: {},
      openedList: [],
      closedList: [],
      selectedStrategy: {},
      MathUtil: MathUtil,
      closedPageInfo: {
        page_count: 0,
        page_no: 1
      },
      openedPageInfo: {
        page_count: 0,
        page_no: 1
      },
      closedListLoading: false,
      closedInited: false,
      openedListLoading: false,
      openedInited: false,
      showClosedDetailPop: false,
      sellNumber: 0
    }
  },
  mounted () {
    this.loadUserAssets()
    this.loadOpenedList()
    ScreenUtil.bindScroll(window, this.onPageScroll)
  },
  methods: {
    onNewsTabItemClicked (newV) {
      this.selectedTab = newV
      if (this.selectedTab === 1) {
        this.closedInited = false
        this.loadClosedList(1)
      } else {
        this.openedInited = false
        this.loadOpenedList(1)
      }
    },
    async loadUserAssets () {
      let res = await queryUserAssets(this.$store.state.User.token)
      if (res.code === 200) {
        this.userAssets = res.data
      }
    },
    async loadOpenedList (page) {
      if (this.openedListLoading) {
        return
      }
      this.openedListLoading = true
      let res = await queryOpenedStrategies(this.$store.state.User.token, page)
      if (res.code === 200) {
        res.data.page.page_count = parseInt(res.data.page.page_count)
        res.data.page.page_no = res.data.page.page_count === 0 ? 0 : parseInt(res.data.page.page_no)
        if (res.data.page.page_no > 1) {
          this.openedList = this.openedList.concat(res.data.page_data)
        } else {
          this.openedList = res.data.page_data
        }
        this.openedPageInfo = res.data.page
      }
      this.openedListLoading = false
      this.openedInited = true
    },
    async loadClosedList (page) {
      if (this.closedListLoading) {
        return
      }
      this.closedListLoading = true
      if (page === 1) {
        this.closedList = []
        this.closedPageInfo.page_count = 0
        this.closedPageInfo.page_no = 1
      }
      let res = await queryClosedStrategies(this.$store.state.User.token, page)
      if (res.code === 200) {
        res.data.page.page_count = parseInt(res.data.page.page_count)
        res.data.page.page_no = res.data.page.page_count === 0 ? 0 : parseInt(res.data.page.page_no)
        if (res.data.page.page_no > 1) {
          this.closedList = this.closedList.concat(res.data.page_data)
        } else {
          this.closedList = res.data.page_data
        }
        this.closedPageInfo = res.data.page
      }
      this.closedListLoading = false
      this.closedInited = true
    },
    loadMoreClosed () {
      if (this.closedPageInfo.page_count === this.closedPageInfo.page_no) {
        return
      }
      this.loadClosedList(parseInt(this.closedPageInfo.page_no) + 1)
    },
    loadMoreOpened () {
      if (this.openedPageInfo.page_count === this.openedPageInfo.page_no) {
        return
      }
      this.loadOpenedList(this.openedPageInfo.page_no + 1)
    },
    onPageScroll () {
      if (this.selectedTab === 1) {
        if (this.closedListLoading === true) {
          return
        }
        if ((ScreenUtil.getScrollY() + document.documentElement.clientHeight + 0) >= document.documentElement.getBoundingClientRect().height) {
          this.loadMoreClosed()
        }
      } else {
        if (this.openedListLoading === true) {
          return
        }
        if ((ScreenUtil.getScrollY() + document.documentElement.clientHeight + 0) >= document.documentElement.getBoundingClientRect().height) {
          this.loadMoreOpened()
        }
      }
    },
    showAutoSellConfig (index) {
      this.selectedStrategy = this.openedList[index]
      this.showAutoSell = false
      this.showAutoSell = true
    },
    onAutoSellClose () {
      this.showAutoSell = false
    },
    onAjustComplete () {
      this.loadOpenedList()
    },
    onSellCancel () {
      this.confirmSellShow = false
    },
    async onSellConfirm () {
      //      if (parseInt(this.selectedStrategy.sell_div_flag) === 1 && parseInt(this.sellNumber) < parseInt(this.selectedStrategy.sell_min_limit)) {
      //        window.EventBus.$emit('globalMessage', '卖出数量不能小于' + this.selectedStrategy.sell_min_limit)
      //        return
      //      }
      let res = await requestStrategySale(this.$store.state.User.token, this.selectedStrategy.id, this.sellNumber)
      if (res.code === 200) {
        this.onSellCancel()
        window.EventBus.$emit('globalMessage', '卖出成功')
        this.loadOpenedList(1)
        this.loadUserAssets()
      }
    },
    showCellConfirm (index) {
      this.selectedStrategy = this.openedList[index]
      this.sellNumber = parseInt(this.selectedStrategy.left_volume)
      this.confirmSellShow = true
    },
    async setAutoDelay (index) {
      let res = await requestCloseAutoDelayStrategy(this.$store.state.User.token, this.openedList[index].id)
      if (res.code === 200) {
        this.loadOpenedList()
      }
    },
    showDetail (id, type) {
      if (window.history && window.history.pushState) {
        window.history.pushState({title: "closeditem", url: "#"}, "closeditem", "#")
      }
      this.showClosedDetailPop = true
      window.addEventListener("popstate", this.onPopState, false)
      window.$('body').addClass('no-scroll')
      this.$refs.contentDetail.$emit('loadData', id, type)
    },
    hideClosedDetail () {
      if (window.history && window.history.pushState) {
        window.history.pushState({title: "news", url: "#"}, "news", "#")
      }
      this.showClosedDetailPop = false
      window.$('body').removeClass('no-scroll')
    },
    onPopState () {
      window.removeEventListener("popstate", this.onPopState)
      this.hideClosedDetail()
    }
  }
}



